    }
}
.wiggle{
    -webkit-animation-name:wiggle-7deg;
    animation-name:wiggle-7deg
}
@-webkit-keyframes wiggle-7deg{
    40%,50%,60%{
        -webkit-transform:rotate(7deg);
        transform:rotate(7deg)
    }
    35%,45%,55%,65%{
        -webkit-transform:rotate(-7deg);
        transform:rotate(-7deg)
    }
    0%,100%,30%,70%{
        -webkit-transform:rotate(0);
        transform:rotate(0)
    }
}
@keyframes wiggle-7deg{
    40%,50%,60%{
        -webkit-transform:rotate(7deg);
        transform:rotate(7deg)
    }
    35%,45%,55%,65%{
        -webkit-transform:rotate(-7deg);
        transform:rotate(-7deg)
    }
    0%,100%,30%,70%{
        -webkit-transform:rotate(0);
        transform:rotate(0)
    }
}
